.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-container {
  display: flex;
  height: 100vh; /* 让整个页面的高度填满屏幕 */
}

.img-container {
  width: 40px;
  height: 40px;
  color:white;
  border-radius: 50%;
  justify-content: center;
  margin: 0 auto;
}

.centered-x {
  display: flex;
  justify-content: center; /* 水平居中 */
}

.centered-y {
  display: flex;
  align-items: center; /* 垂直居中 */
}

.area_Navbar {
  margin-left: 80px;
}
